'use client';
import { useToast } from "@/hooks/use-toast";
import { motion } from 'framer-motion';
import {
    Calendar,
    Ticket,
    Users,
    DollarSign,
    Clock,
    ChevronRight,
    ChevronLeft,
    Search,
    List,
    Flame,
    History,
    Sparkles,
    Edit,
    Eye,
} from 'lucide-react';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { Button } from '@/components/ui/button';
import { Calendar as CalendarComp } from '@/components/ui/calendar';
import { Input } from '@/components/ui/input';
import { Tabs, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { useEffect, useState } from 'react';
import { events, EventType } from '@/constants/events';
import Link from 'next/link';
import { useDebounce} from 'use-debounce';
import { env } from "process";

// تعريفات الحركات
const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
        opacity: 1,
        transition: {
            staggerChildren: 0.1,
            delayChildren: 0.2,
        },
    },
};

const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: {
        opacity: 1,
        y: 0,
        transition: {
            duration: 0.5,
            ease: 'easeOut',
        },
    },
    hover: {
        scale: 1.02,
        transition: {
            duration: 0.3,
        },
    },
};

// تسميات الفلاتر
const filterLabels = [
    { label: 'All', icon: List },
    { label: 'Active', icon: Flame },
    { label: 'Past', icon: History },
    { label: 'New', icon: Sparkles },
    { label: 'Latest', icon: Clock },
    { label: 'Draft', icon: Edit },
];



const HostDashboard = () => {
    const [event, setEvents] = useState<EventType[]>([]);
    // حالات المكون
    const [date, setDate] = useState<Date | undefined>(new Date());
    const [activeFilter, setActiveFilter] = useState<string>('all');
    const [searchQuery, setSearchQuery] = useState('');
    const [debouncedQuery] = useDebounce(searchQuery, 300);
    const [currentPage, setCurrentPage] = useState(1);
    const [eventsPerPage] = useState(6);
    // 'all' | 'active' | 'past' | 'new' | 'latest' | 'draft'
    const [isLoading, setIsLoading] = useState(true);
    const { toast } = useToast();
    let isStudent, isHost, isAdmin = true;
    useEffect(() => {
        if (isLoading) return;
        if (!isStudent && !isHost && !isAdmin) return;

        const fetchEvents = async () => {
            try {
                setIsLoading(true);
                const response = await fetch(`${env}`, {
                    method: "GET",
                })
                console.log("status : ", response.status)
                const data = await response.json();
                setEvents(data);

                if (!response.ok) {
                    toast({
                        variant: "destructive",
                        title: "خطأ!",
                        description: "فشل في تحميل الحجوزات",
                    });
                }
            } catch (err: any) {
                toast({
                    variant: "destructive",
                    title: "خطأ!",
                    description: "حدث خطأ أثناء جلب البيانات ، تعذر الاتصال بالسيرفر حاول مرة أخرى.",
                });
            } finally {
                setIsLoading(false);
            }
        }
        fetchEvents();
    }, [activeFilter, isStudent, isHost, isAdmin]);


    // تصفية الأحداث
    const filteredEvents = (events || []).filter(
        (event) =>
            (activeFilter === 'all' || event.status === activeFilter) &&
            event.title.toLowerCase().includes(debouncedQuery.toLowerCase())
    );

    // Pagination
    const currentEvents = filteredEvents.slice(
        (currentPage - 1) * eventsPerPage,
        currentPage * eventsPerPage
    );
    const totalPages = Math.ceil(filteredEvents.length / eventsPerPage);

    const paginate = (pageNumber: number) => setCurrentPage(pageNumber);

    // إحصائيات الأحداث
    const stats = {
        totalEvents: events?.length || 0,
        upcomingEvents: events?.filter((e) => new Date(e.date) > new Date()).length || 0,
        totalAttendees: events?.reduce((sum, e) => sum + (e.attendees || 0), 0) || 0,
        totalRevenue: events?.reduce((sum, e) => sum + (e.revenue || 0), 0) || 0,
    };

    // حالة التحميل
    if (!events) {
        return (
            <div className="flex items-center justify-center h-[70vh]">
                <div className="animate-spin rounded-full h-16 w-16 border-t-4 border-b-4 border-purple-500"></div>
            </div>
        );
    }

    return (
        <motion.div
            initial="hidden"
            animate="visible"
            variants={containerVariants}
            className="p-6 space-y-8 max-w-7xl mx-auto"
        >
            {/* Header */}
            <motion.div variants={itemVariants}>
                <Card className="bg-gradient-to-r from-purple-800 to-pink-700 text-white shadow-xl">
                    <div className="p-8">
                        <motion.h2
                            className="text-3xl font-bold mb-2"
                            initial={{ x: -20 }}
                            animate={{ x: 0 }}
                        >
                            Welcome back, Host!
                        </motion.h2>
                        <p className="opacity-90 text-purple-100">
                            Here's what's happening with your events today.
                        </p>
                    </div>
                </Card>
            </motion.div>

            {/* Stats Cards */}
            <motion.div
                className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4"
                variants={containerVariants}
            >
                {[
                    {
                        title: 'Total Events',
                        value: stats.totalEvents,
                        icon: Ticket,
                        trend: '+12%',
                    },
                    {
                        title: 'Upcoming Events',
                        value: stats.upcomingEvents,
                        icon: Calendar,
                        trend: '+2',
                    },
                    {
                        title: 'Total Attendees',
                        value: stats.totalAttendees,
                        icon: Users,
                        trend: '+24',
                    },
                    {
                        title: 'Total Revenue',
                        value: `${stats.totalRevenue.toLocaleString()}`,
                        icon: DollarSign,
                        trend: '+15%',
                    },
                ].map((stat, index) => (
                    <motion.div key={stat.title} variants={itemVariants}>
                        <Card className="hover:shadow-lg transition-shadow h-full">
                            <CardHeader className="flex flex-row items-center justify-between">
                                <CardTitle className="text-sm font-medium text-gray-500">
                                    {stat.title}
                                </CardTitle>
                                <stat.icon className="h-6 w-6 text-purple-500" />
                            </CardHeader>
                            <CardContent>
                                <div className="text-2xl font-bold">{stat.value}</div>
                                <p className="text-sm text-muted-foreground">
                                    {stat.trend} from last month
                                </p>
                            </CardContent>
                        </Card>
                    </motion.div>
                ))}
            </motion.div>

            {/* Controls */}
            <motion.div
                className="flex flex-col md:flex-row gap-4 items-start md:items-center"
                variants={itemVariants}
            >
                <div className="relative flex-1 max-w-md">
                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                        <Search className="h-5 w-5 text-purple-400" />
                    </div>
                    <Input
                        placeholder="Search events..."
                        className="pl-10 pr-4 py-2 rounded-full border border-purple-300 focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                        value={searchQuery}
                        onChange={(e) => {
                            setSearchQuery(e.target.value);
                            setCurrentPage(1);
                        }}
                    />
                </div>

                <Tabs
                    value={activeFilter}
                    onValueChange={(val) => {
                        setActiveFilter(val);
                        setCurrentPage(1);
                    }}
                    className="flex-1"
                >
                    <TabsList className="bg-transparent gap-2 flex-wrap justify-start">
                        {filterLabels.map(({ label, icon: Icon }) => (
                            <TabsTrigger
                                value={label.toLowerCase()}
                                key={label}
                                className="data-[state=active]:bg-purple-600 data-[state=active]:text-white rounded-full px-4 py-2 transition-all text-sm"
                            >
                                <div className="flex items-center gap-2">
                                    <Icon className="h-4 w-4" />
                                    {label}
                                </div>
                            </TabsTrigger>
                        ))}
                    </TabsList>
                </Tabs>

                <CalendarComp
                    mode="single"
                    selected={date}
                    onSelect={setDate}
                    className="rounded-lg border shadow-sm"
                />
            </motion.div>

            {/* Events Grid */}
            {currentEvents.length > 0 ? (
                <>
                    <motion.div
                        className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6"
                        variants={containerVariants}
                    >
                        {currentEvents.map((event) => (
                            <motion.div
                                key={event.id}
                                variants={itemVariants}
                                whileHover="hover"
                                className="bg-gradient-to-br from-purple-900/80 to-pink-800/90 rounded-xl overflow-hidden shadow-lg border border-purple-500/20"
                            >
                                <div className="p-6">
                                    <div className="flex justify-between items-start mb-4">
                                        <h3 className="text-xl font-bold text-white">{event.title}</h3>
                                        <span
                                            className={`px-3 py-1 rounded-full text-xs ${event.status === 'active'
                                                ? 'bg-green-500/20 text-green-200'
                                                : event.status === 'draft'
                                                    ? 'bg-yellow-500/20 text-yellow-200'
                                                    : 'bg-gray-500/20 text-gray-200'
                                                }`}
                                        >
                                            {event.status}
                                        </span>
                                    </div>

                                    <div className="flex items-center gap-3 text-purple-200 mb-4">
                                        <Calendar className="h-5 w-5" />
                                        <span>{new Date(event.date).toLocaleDateString()}</span>
                                    </div>

                                    <div className="flex items-center gap-3 text-purple-200 mb-4">
                                        <Users className="h-5 w-5" />
                                        <span>{event.attendees || 0} attendees</span>
                                    </div>

                                    <div className="flex justify-between items-center">
                                        {event.revenue && (
                                            <div className="flex items-center gap-2 text-purple-200">
                                                <DollarSign className="h-5 w-5" />
                                                <span>${event.revenue.toLocaleString()}</span>
                                            </div>
                                        )}

                                        <div className="flex gap-2">
                                            <Button
                                                variant="ghost"
                                                className="bg-purple-600/30 hover:bg-purple-600/50 text-white"
                                                asChild
                                            >
                                                <Link href={`/events/${event.id}/edit`}>
                                                    <Edit className="h-4 w-4 mr-2" />
                                                    Edit
                                                </Link>
                                            </Button>
                                            <Button
                                                variant="ghost"
                                                className="bg-pink-600/30 hover:bg-pink-600/50 text-white"
                                                asChild
                                            >
                                                <Link href={`/events/${event.id}`}>
                                                    <Eye className="h-4 w-4 mr-2" />
                                                    View
                                                </Link>
                                            </Button>
                                        </div>
                                    </div>
                                </div>
                            </motion.div>
                        ))
                        }
                    </motion.div >

                    {/* Pagination */}
                    {
                        totalPages > 1 && (
                            <motion.div
                                initial={{ opacity: 0, y: 20 }}
                                animate={{ opacity: 1, y: 0 }}
                                className="flex justify-center mt-8"
                            >
                                <div className="flex items-center gap-1 bg-purple-900/20 backdrop-blur-sm p-1 rounded-full">
                                    <Button
                                        variant="ghost"
                                        size="icon"
                                        onClick={() => paginate(Math.max(1, currentPage - 1))}
                                        disabled={currentPage === 1}
                                        className="rounded-full hover:bg-purple-700/50"
                                    >
                                        <ChevronLeft className="h-5 w-5" />
                                    </Button>

                                    {Array.from({ length: Math.min(5, totalPages) }).map((_, i) => {
                                        let pageNum;
                                        if (totalPages <= 5) {
                                            pageNum = i + 1;
                                        } else if (currentPage <= 3) {
                                            pageNum = i + 1;
                                        } else if (currentPage >= totalPages - 2) {
                                            pageNum = totalPages - 4 + i;
                                        } else {
                                            pageNum = currentPage - 2 + i;
                                        }

                                        return (
                                            <Button
                                                key={pageNum}
                                                variant={currentPage === pageNum ? 'default' : 'ghost'}
                                                size="icon"
                                                onClick={() => paginate(pageNum)}
                                                className={`rounded-full ${currentPage === pageNum
                                                    ? 'bg-purple-600 text-white'
                                                    : 'hover:bg-purple-700/50'
                                                    }`}
                                            >
                                                {pageNum}
                                            </Button>
                                        );
                                    })}

                                    {totalPages > 5 && currentPage < totalPages - 2 && (
                                        <span className="px-2 text-purple-300">...</span>
                                    )}

                                    {totalPages > 5 && currentPage < totalPages - 2 && (
                                        <Button
                                            variant="ghost"
                                            size="icon"
                                            onClick={() => paginate(totalPages)}
                                            className="rounded-full hover:bg-purple-700/50"
                                        >
                                            {totalPages}
                                        </Button>
                                    )}

                                    <Button
                                        variant="ghost"
                                        size="icon"
                                        onClick={() => paginate(Math.min(totalPages, currentPage + 1))}
                                        disabled={currentPage === totalPages}
                                        className="rounded-full hover:bg-purple-700/50"
                                    >
                                        <ChevronRight className="h-5 w-5" />
                                    </Button>
                                </div>
                            </motion.div >
                        )
                    }
                </>
            ) : (
                <motion.div
                    className="text-center py-16 space-y-4"
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                >
                    <div className="text-2xl font-bold">No events found</div>
                    <p className="text-muted-foreground">
                        {searchQuery
                            ? 'Try a different search term'
                            : 'Create your first event to get started'}
                    </p>
                    <Button asChild>
                        <Link href="events/create">Create New Event</Link>
                    </Button>
                </motion.div>
            )}
        </motion.div >
    );
};

export default HostDashboard;